<template>
  <div class='page-wrapper'>
    <base-header circle underline>
      <base-button
        circle
        flat
        class='mx-auto back-button left-align'
        :to="{ name: 'parties' }"
        slot='before'
      >
        <v-icon>arrow_back</v-icon>
      </base-button>

      <base-button
        circle
        flat
        class='mx-auto back-button right-align'
        :to="{ name: 'party-settings', params: { id } }"
        slot='after'
      >
        <v-icon>settings</v-icon>
      </base-button>
    </base-header>

    <div class='page'>
      <p class='party__title headline'>{{ party ? party.name : '' }}</p>

      <v-divider style='divider'></v-divider>

      <v-tabs slider-color="primary" color="transparent" grow class='tabs'>
        <v-tab ripple :key='queue' class='tabs__tab'>
          Queue
        </v-tab>
        <v-tab ripple :key='suggestions' class='tabs__tab'>
          Suggestions
        </v-tab>
        <v-tab-item :key='queue'>
          WOW
        </v-tab-item>
        <v-tab-item :key='suggestions'>
          NOT WOW
        </v-tab-item>
      </v-tabs>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: String,
      required: true
    }
  },
  computed: {
    party() {
      return this.$store.state.parties.find(
        party => party._id === this.$props.id
      );
    },
    allParties() {
      this.$store.state.parties;
    }
  }
};
</script>

<style scoped lang="scss">
.page {
  max-width: 65rem;
  margin: 0 auto;
  .party__title {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;

    text-align: center;
    text-transform: uppercase;
    margin-top: 3rem;
    color: rgba(#66fcf1, 0.85);
    letter-spacing: 0.3rem !important;
  }
}
.left-align {
  margin-left: 0.5rem !important;
  margin-right: auto;
}
.right-align {
  margin-right: 0.5rem !important;
  margin-left: auto;
}
.tabs {
  .tabs__tab {
    width: 50% !important;
    margin-bottom: 1rem;
  }
}
</style>




